import { visulizations } from "./visualizations";
import { pizzaData } from "../utils/pizzaData";
import { debounce } from "../utils/debounce";
const debouncedPizzaInit = debounce(function (selection, data, ringColumn, ringSet, sliceColumn, sliceSet, vis) {
    vis.pizzaChart.init(selection, data, ringColumn, ringSet, sliceColumn, sliceSet);
}, 400);
const debounceBarInit = debounce(function (selection, data, fillColor, width, height, padding, vis) {
    vis.barChart.init(selection, data, fillColor, width, height, padding);
}, 200);
const debouncedBarEnter = debounce(function (data, fillColor, vis, width) {
    vis.barChart.chart.data(data);
    if (width)
        vis.barChart.chart.width(width);
    setTimeout(() => {
        vis.barChart.chart.fillColor(fillColor);
    }, 200);
}, 100);
const debouncePizzaEnter = debounce(function ({ data, ringColumn, ringSet, sliceColumn, sliceSet, vis }) {
    if (data)
        vis.pizzaChart.chart.data(data);
    if (ringColumn)
        vis.pizzaChart.chart.ringColumn(ringColumn);
    if (ringSet)
        vis.pizzaChart.chart.ringSet(ringSet);
    if (sliceColumn)
        vis.pizzaChart.chart.sliceColumn(sliceColumn);
    if (sliceSet)
        vis.pizzaChart.chart.sliceSet(sliceSet);
}, 200);
export const slides = {
    "0": {
        visulization: "barChart",
        mounted: false,
        data: [2, 5, 4, 1, 2, 6, 5],
        onInit: function (selection) {
            const boundingRect = selection.node().getBoundingClientRect();
            this.width = (boundingRect.width * 3) / 4;
            this.height = boundingRect.height;
            debounceBarInit(selection, this.data, "steelblue", 0, this.height, 1, visulizations);
        },
        onEnter: function () {
            if (!visulizations.barChart.chart)
                return;
            debouncedBarEnter(this.data, "steelblue", visulizations);
        },
        onExit: function () {
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.exit();
        },
        onEnterScrollDown: function (ratio) {
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.width(ratio * this.width);
        },
        onExitScrollDown: (ratio) => {
            // console.log("scrolling down exit", ratio);
        },
        onEnterScrollUp: (ratio) => {
            // console.log("scrolling up enter", ratio);
        },
        onExitScrollUp: function (ratio) {
            // console.log("scrolling up exit", ratio);
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.width(ratio * this.width);
            if (ratio < 0.3 && visulizations.barChart.chart)
                visulizations.barChart.chart.width(0);
        },
    },
    "1": {
        visulization: "barChart",
        mounted: false,
        data: [17, 4, 18, 9, 2, 21, 88, 9, 12, 75, 33, 20, 18, 15, 17, 16, 18, 23],
        onInit: function (selection) {
            const boundingRect = selection.node().getBoundingClientRect();
            this.width = boundingRect.width;
            this.height = boundingRect.height;
            debounceBarInit(selection, this.data, "coral", this.width, this.height, 1, visulizations);
            this.mounted = true;
        },
        onEnter: function () {
            if (!visulizations.barChart.chart)
                return;
            const width = slides["0"].width;
            debouncedBarEnter(this.data, "coral", visulizations, width);
        },
        onExit: function () {
            if (visulizations.barChart.chart)
                visulizations.barChart.chart.exit();
            this.mounted = false;
        },
        onEnterScrollDown: (ratio) => { },
        onExitScrollDown: (ratio) => {
            console.log("scrolling down exit", ratio);
        },
        onEnterScrollUp: (ratio) => {
            console.log("scrolling up enter", ratio);
        },
        onExitScrollUp: (ratio) => { },
    },
    "2": {
        visulization: "pizzaChart",
        mounted: false,
        data: pizzaData,
        onInit: function (selection) {
            this.mounted = true;
            const data = this.data;
            let sliceColumn = "eaten", ringColumn = "component", sliceSet = [...new Set(data.map((d) => d[sliceColumn]))], ringSet = [...new Set(data.map((d) => d[ringColumn]))];
            setTimeout(() => {
                debouncedPizzaInit(selection, this.data, ringColumn, ringSet, sliceColumn, sliceSet, visulizations);
            }, 200);
        },
        onEnter: () => { },
        onExit: function () {
            if (visulizations.pizzaChart.chart)
                debouncePizzaEnter({ vis: visulizations, data: [] });
            this.mounted = false;
        },
        onEnterScrollDown: (ratio) => {
            // if (!visulizations.pizzaChart.chart) return;
            // debouncePizzaEnter({ringSet:['meat', 'topping', 'leaf', 'flower', 'whole', 'root', 'fruit', 'top', 'bulb'], vis: visulizations});
        },
        onExitScrollDown: (ratio) => {
            console.log("scrolling down exit", ratio);
            if (!visulizations.pizzaChart.chart)
                return;
            debouncePizzaEnter({ ringSet: ['meat', 'topping', 'leaf', 'flower', 'whole', 'root', 'fruit', 'top', 'bulb'], vis: visulizations });
        },
        onEnterScrollUp: (ratio) => {
            if (!visulizations.pizzaChart.chart)
                return;
            debouncePizzaEnter({ ringSet: ['whole', 'meat', 'topping', 'leaf', 'flower', 'root', 'fruit', 'top', 'bulb'], vis: visulizations });
        },
        onExitScrollUp: (ratio) => {
            console.log("scrolling up exit", ratio);
        },
    },
};
