import { barChart } from "../visulizations/barChart";
import { pizzaChart } from "../visulizations/pizzaChart";
export const visulizations = {
    barChart: {
        name: "barChart",
        mounted: false,
        generator: barChart,
        chart: null,
        init: function (selection, data, fillColor, width, height, padding) {
            this.chart = this.generator();
            this.chart.data(data);
            this.chart.fillColor(fillColor);
            this.chart.width(width);
            this.chart.height(height);
            this.chart.padding(padding);
            selection.call(this.chart);
        },
    },
    growingCircle: {
        name: "growingCircle",
        mounted: false,
        generator: barChart,
        chart: null,
        init: function (selection, data, fillColor, width, height, padding) {
            this.chart = this.generator();
            this.chart.data(data);
            this.chart.fillColor(fillColor);
            this.chart.width(width);
            this.chart.height(height);
            this.chart.padding(padding);
            setTimeout(() => {
                selection.call(this.chart);
            }, 250);
        },
    },
    pizzaChart: {
        name: "pizzaChart",
        generator: pizzaChart,
        mounted: false,
        chart: null,
        init(selection, data, ringColumn, ringSet, sliceColumn, sliceSet) {
            this.chart = this.generator();
            this.chart.data(data);
            this.chart.ringColumn(ringColumn);
            this.chart.ringSet(ringSet);
            this.chart.sliceColumn(sliceColumn);
            this.chart.sliceSet(sliceSet);
            selection.call(this.chart);
        },
    },
};
